@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro:wght@300;400&Poppins:800&family=Ubuntu:wght@400,700&display=swap);
html {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.mobileContainer {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: none;
    z-index: 999;
}
.mobileContainer  >img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

@media (max-width: 640px) {
    .mobileContainer {
        display: block;
    }
}
.App {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  font-family: "SF Mono";
  letter-spacing: 2px;
  font-weight: 300;
  color: white;
  background-size: cover;
  background-repeat: no-repeat;
}
.App .header {
  font-family: "Poppins", sans-serif;
  font-size: 18px;
  letter-spacing: 8.67px;
  color: #393939;
}
.App .splitContainer {
  display: flex;
  flex-wrap: nowrap;
  width: 100%;
  height: 100%;
  padding: 20px 40px;
}
.App .splitContainer .screen {
  height: 100%;
  flex-basis: 32%;
}
.App .splitContainer .screen1, .App .splitContainer .screen2, .App .splitContainer .screen3 {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.App .splitContainer .screen1 > div, .App .splitContainer .screen2 > div, .App .splitContainer .screen3 > div {
  flex-basis: 49%;
}
.App .splitContainer .screen2 {
  background: rgba(255, 255, 255, 0.03);
  align-items: center;
}
.App .splitContainer .screen2 .inputItem {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.App .mode {
  display: flex;
  justify-content: center;
  margin: 0 20px;
  margin-top: 125px;
}
.App .mode .item {
  cursor: not-allowed;
  font: 200 50px "Roboto Mono", sans-serif;
  color: #24FF43;
}
.App .subtitle {
  font-size: 12px;
  letter-spacing: 2.13px;
  line-height: 2;
  width: 100%;
}
.App .subtitle .suffix .options {
  display: flex;
  align-items: center;
  height: 28px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border: 2px solid #808080;
  padding: 0;
  border-radius: 4px;
  cursor: pointer;
}
.App .subtitle .suffix .options .item {
  height: 100%;
  padding: 0px 10px;
  border-radius: 2px;
}
.App .subtitle .suffix .options .item.activeMode {
  background: #24FF43;
  color: black;
}
.App .subtitle .digits span {
  margin-right: 10px;
  margin-top: -5px;
  font-size: 14px;
}
.App .subtitle .digits .limitInput {
  width: 20px;
  background: black;
  border: none;
  border-bottom: solid 3px #24FF43;
  outline: none;
  color: #24FF43;
  text-align: center;
  padding: 5px 10px;
}
.App form {
  flex-basis: 49%;
  margin-top: 50px;
  width: 100%;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}
.App form > div {
  position: relative;
}
.App form > div::before {
  content: ">";
  color: #24FF43;
  position: absolute;
  left: 18px;
  top: 40px;
}
.App form input {
  width: 300px;
  height: 70px;
  background: black;
  color: #24FF43;
  outline: none;
  padding: 15px 25px 15px 40px;
  font-family: "Source Code Pro";
  font-weight: 400;
  font-size: 20px;
  text-transform: uppercase;
  letter-spacing: 5px;
  border: none;
}
.App form button {
  margin-left: -60px;
  background-color: inherit;
  border: none;
  outline: none;
  color: white;
  font-size: 3rem;
  cursor: pointer;
}
.App .reset {
  background-color: black;
  border: none;
  outline: none;
  color: red;
  cursor: pointer;
  text-transform: uppercase;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  letter-spacing: 2px;
  margin-top: 25px;
}
.App .reset:hover {
  color: #24FF43;
}
.App .errorMessage {
  margin: 50px auto;
  height: 20px;
  font-size: 1rem;
  color: red;
}
.App .scoreContainer {
  font: 200 75px "Roboto Mono", sans-serif;
  text-align: center;
  flex-basis: 70% !important;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.App .scoreContainer .score {
  color: white;
}
.App .scoreContainer .passive {
  font: 18px "SF Mono", sans-serif;
  color: white;
  letter-spacing: 4.5px;
}
